.popup__title {
  max-width: 330px;
  margin: 0 auto;
  padding: 0;
  font-family: 'Inter', Arial, Helvetica;
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
}

@media screen and (max-width: 338px) {
  .popup__title {
    max-width: 238px;
    font-size: 18px;
    line-height: 22px;
    margin-left: 22px;
  }
}
